* {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  height: 100vh;
  color: #1d1d1d;
  background-image: url("https://images7.alphacoders.com/938/thumb-1920-938300.jpg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Open Sans, Helvetica Neue, sans-serif;
  line-height: 1.5;
  overflow: hidden;
}

#search-box {
  font: inherit;
  max-width: 320px;
  height: 40px;
  background-color: rgba(237, 237, 237, .9);
  border: 2px solid #363643;
  border-radius: 5px;
  margin-bottom: 16px;
  padding: 8px;
  box-shadow: -5px -5px 20px rgba(249, 249, 249, .9), -4px -4px 5px rgba(249, 249, 249, .9), 5px 5px 20px rgba(0, 0, 0, .2), 4px 4px 5px rgba(0, 0, 0, .3);
