  padding: 0;
  list-style: none;
}

.country-list {
  padding: 20px 40px 20px 20px;
}

.country-list__item {
  background-color: rgba(234, 184, 184, .435);
  border-radius: 4px;
  margin: 20px;
  display: flex;
}

.country-list__img {
  margin: 10px;
}

.country-list__name {
  margin: 10px;
